<template>
  <div class="view">
    <van-nav-bar
      title=""
      left-text=""
      left-arrow
      @click-left="$router.go(-1)"
    />
    <div class="content">
      <van-field v-model="form.name" placeholder=""/>
      <van-field v-model="form.price" placeholder=""/>
      <van-field v-model="form.depreciation" placeholder=""/>
      <van-field v-model="form.description" placeholder=""/>
      <van-field v-model="form.position" placeholder=""/>

      <div class="upload-wrap">
        <div class="preview-box" v-for="(item, index) in pickList" :key="index" >
          <img class="pre-item" :src="item.content"/>
          <div @click="removeItem(index)">
            <div>%{{ item.percent }}</div>
            <img class="icon-remove" src="./img/remove.png" alt="">
          </div>
        </div>
        <van-uploader class="touch-area" :after-read="onRead">
          <template v-show="pickList.length===0">
            <img class="icon-add" src="./img/add.png">
          </template>
        </van-uploader>
      </div>

      <van-cell title="">
        <van-switch v-model="checked"/>
      </van-cell>
    </div>
    <van-button class="btm-fixed" size="large" type="primary" @click="doCreateProduct"></van-button>
  </div>
</template>

<script>
import Vue from 'vue';
import { Field, NavBar, Switch, Cell, CellGroup, Uploader, Toast, Button } from 'vant';
import ynowApi from '../../api/ynow';
import * as qiniu from 'qiniu-js';

Vue
  .use(NavBar)
  .use(Field)
  .use(Switch)
  .use(Cell)
  .use(CellGroup)
  .use(Button)
  .use(Uploader);

export default {
  data () {
    return {
      checked: '',
      form: {
        price: '100000',
        name: 'iphone6Splus64g iphone6splus64g',
        position: '',
        description: 'iphone6Splus64g iphone6splus64g',
        depreciation: '9'
      },
      pickList: [],
      token: ''
    };
  },
  mounted () {
    this.doGet7nToken();
  },
  methods: {
    onRead (file) {
      this.pickList.push(file);
      this.uploadImg(this.pickList.length - 1, file.file);
    },
    removeItem (index) {
      this.pickList.splice(index, 1);
    },
    doGet7nToken () {
      ynowApi.get7nToken().then(res => {
        this.token = res.data.token;
      });
    },
    uploadImg (index, file, callback) {
      const vm = this;
      const observable = qiniu.upload(file, file.name, this.token, { }, { });
      observable.subscribe({
        next (res) {
          vm.pickList[index]['percent'] = res.total.percent;
          vm.pickList = [].concat(vm.pickList);
        },
        error (error) {
          console.log(error);
          Toast(error);
        },
        complete (res) {
          vm.pickList[index]['url'] = `//c1.airtlab.com/${res.key}`;
          vm.pickList = [].concat(vm.pickList);
        }
      });
    },
    doCreateProduct () {
      let imgs = this.pickList.map(element => element.url);
      imgs = JSON.stringify(imgs);
      const params = Object.assign({
        imgs
      }, this.form);
      ynowApi.createXzProduct(params).then(res => {
        Toast('');
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.upload-wrap {
  float: right;
  padding: 20px 0;
  .touch-area,
  .pre-item {
    float: right;
    width: 80px;
    height: 80px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid #eee;
  }
  .icon-add {
    position: absolute;
    width: 30px;
    height: 30px;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
  }
  .icon-remove {
    display: block;
    width: 24px;
    height: 24px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  .preview-box {
    float: right;
  }
}

.btm-fixed {
  position: absolute;
  bottom: 0;
}
</style>



